<template>
	<view class="page">
		<view class="cu-list menu-avatar margin-top">
			<view class="cu-item" v-for="(item, index) in getList" :key="index" >
				<view class="content">
					<view class="text-sm flex">
						<view class="text-cut">{{item.name}}</view>
					</view>
				</view>
				<button class="cu-btn round bg-blue margin-right-xs" @click="editMeasuringType(item)">编辑</button>
				<button class="cu-btn round bg-red" @click="deleteMeasuringType(item)">删除</button>
			</view>
		</view>
		<view class="cu-modal" :class="modalName=='DialogModal1'?'show':''">
			<view class="cu-dialog">
				<view class="cu-bar bg-white justify-end">
					<view class="action" @tap="hideModal">
						<text class="cuIcon-close text-red"></text>
					</view>
				</view>
				<view class="margin">
					<view class="cu-form-group">
						<view class="title">名称</view>
						<input maxlength="30" class="text-right" placeholder="名称" v-model="details.name" placeholder-class="placeholder"></input>
					</view>
				</view>
				
				<view class="cu-bar bg-white justify-end">
					<view class="action">
						<button class="cu-btn line-red text-red" @tap="hideModal">取消</button>
						<button class="cu-btn bg-red margin-left" @tap="setMeasuringType">确定</button>
		
					</view>
				</view>
			</view>
		</view>
		<!-- 底部 -->
		<view class="footer">
			<view class="submit-content go-indent">
				<text class="submit-red" @click="addMeasuringType()">添加测量类型</text>
			</view>
		</view>
	</view>
</template> 

<script>
	import MeasuringType from '../../api/measuringType'
	export default {
		data() {
			return {
				page:1,
				getList: [],
				details: {
					name: null
				},
				modalName: ''
				}
		},
		
		onLoad: function(options) {
			this.loadData()
		},
		methods: {
			//获取订单列表
			async loadData(source){
				const that = this
				await MeasuringType.getList({
					limit: 100,
					page: this.page
				},function(res){
					that.getList = res.data
				})
			},
			hideModal(e) {
				this.modalName = null
			},
			//编辑
			goDetails(){
				uni.navigateTo({
					url: `/pages/store/add-staff`
				})
			},
			//添加测量类型
			addMeasuringType(){
				this.details.name = null
				this.modalName = 'DialogModal1'
			},
			//编辑测量类型
			editMeasuringType(res){
				this.details = res
				this.modalName = 'DialogModal1'
			},
			//删除
			deleteMeasuringType(data){
				const that = this
				uni.showModal({
				    title: '提示',
				    content: '是否确认删除？',
				    success: function (res) {
				        if (res.confirm) {
				            MeasuringType.deleteSubmit(data.id,function(res){
				            	that.$api.msg(`测量类型删除成功`);
				            	that.page = 1
				            	that.loadData()
				            	that.hideModal()
				            })
				        }
				    }
				});
				
			},
			//添加
			setMeasuringType(){
				let data = this.details
				const that = this
				if(!data.name){
					this.$api.msg('请填写名片');
					return;
				}
				if(data.id){
					MeasuringType.updateSubmit(data,function(res){
						that.$api.msg(`测量类型添加成功`);
						that.page = 1
						that.loadData()
						that.hideModal()
					})
				}else{
					MeasuringType.createSubmit(data,function(res){
						that.$api.msg(`测量类型更新成功`);
						that.page = 1
						that.loadData()
						that.hideModal()
					})
				}
			}
		},
	}
</script>

<style lang="scss">
	page, .page{
		background: $page-color-base;
		height: 100%;
		
	}
	.footer{
		position: fixed;
		left: 0;
		bottom: 0;
		z-index: 995;
		display: flex;
		align-items: center;
		width: 100%;
		height: 90upx;
		justify-content: space-between;
		font-size: 30upx;
		background-color: #fff;
		z-index: 998;
		color: $font-color-base;
		box-shadow: 0 -1px 5px rgba(0,0,0,.1);
		.price-content{
			padding-left: 30upx;
		}
		.price-tip{
			color: $base-color;
			margin-left: 8upx;
		}
		.price{
			font-size: 36upx;
			color: $base-color;
		}
		.submit{
			display:flex;
			align-items:center;
			justify-content: center;
			width: 280upx;
			height: 100%;
			color: #fff;
			font-size: 32upx;
			background-color: $base-color;
		}
		.submit-content{
			display:flex;
			flex-direction:row-reverse;
			align-items:center;
			justify-content: center;
			height: 100%;
			.submit-red{
				display:flex;
				flex-direction:row-reverse;
				align-items:center;
				justify-content: center;
				width: 100%;
				height: 100%;
				color: #fff;
				font-size: 32upx;
				background-color: $base-color;
			}
			.submit-default{
				display:flex;
				align-items:center;
				justify-content: center;
				width: 200upx;
				height: 100%;
				color: #fff;
				font-size: 32upx;
				background-color: #909399;
			}
		}
		.go-indent{
			width: 100%;
		}
	}
	.cu-list{
		padding-bottom: 80upx;
	}
	.cu-list.menu-avatar>.cu-item .content{
		left: 30rpx;
	}
</style>
